.cards{
    width: 100%;
    height: auto;
}




.card{
   margin: 6%;
    transition: all 0
    4s cubic-bezier(0.175, 0.885,0,1);
    background-color: #fff;
    width:21.25%;
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px rgba(0,0,0,0.1);
    float: left;
}

.card:hover{
    box-shadow: 0px 30px 18px -8px rgba(0,0,0,0.1);
    transform: scale(1.05,1.05);
}


.card-img{
    height:235px;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    width: 100%;
}

button{
    padding: 5px 8px;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #868686;
    outline: none;
    border: 1ox solid black;
    cursor: pointer;
}

button:hover{
    background-color: azure;
}

.card-title{
    margin-top: 5px;
    margin-bottom: 10px;
    background-color: #fff;
    text-transform: capitalize;
}

.card-category{
    text-transform: uppercase;
    font-size: 13px;
    ;letter-spacing: 2px;
    font-weight: 500;
    color:#868686;
    background-color: #fff;

}


.card-info{
    background-color: #fff;
    border-top-left-radius: 12px;
    border-bottom-right-radius: 12px;
    padding: 16px 24px 24px 24px;
    background-color: #fff;
}


@media (max-width:768px) {
    .card{
        float:none;
        margin: 5%;
        width:90%
    }
    
}